import React,{useState} from "react";
import ToDolists from "./ToDolist";

const App=()=>{
    const [item, setItem] = useState("");
    const [getItem,getNewItem]=useState([]);
    var showItem = (e)=>{
        const value = e.target.value;
        setItem(value)
    };
    const show = ()=>{
        getNewItem((oldItem)=>{
                return [...oldItem,item]
        });
        setItem("");
    };
    const deleteItems=(id)=>{
        console.log("deleted");
        getNewItem((oldItem)=>{
            return oldItem.filter((arr, index)=>{
                return index !== id;
            })
        })
    }
    

    return (
        <>
           <div className="main_div">
               <div className="center_div">
                   <h1>ToDo List</h1>
                   <input type="text" placeholder="Enter a Item" onChange={showItem}  value={item}/>
                   <button onClick={show}>+</button>
                   <ol>
                     { getItem.map((value,index)=>{
                      return  <ToDolists 
                            text={value}
                            id={index}
                            key={index} 
                            onSelect={deleteItems}
                        />
                      })}
                   </ol>
               </div>
           </div>
        </>
    )
};

export default App;